.technologies {
	&__heading {
		margin: 0 0 50px;
	}
	&__items {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-column-gap: 30px;
		grid-row-gap: 15px;
	}
	&__item {
		background: $color-default-white;
		border: 1px solid $color-hawkes-blue;
		border-radius: 5px;
		transition: background-color $trans-default;
		padding: 25px;
		position: relative;
		&-row {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin: 0 0 25px;
		}
		&-icon {
			color: $color-bunting;
			transition: color $trans-default;
		}
		&-num {
			font-weight: 800;
			font-size: 80px;
			line-height: 98px;
			color: rgba($color-lavender, 0.5);
			transition: color $trans-default;
			position: absolute;
			top: 6px;
			right: 15px;
		}
		&-title {
			font-weight: 700;
			font-size: 16px;
			line-height: 26px;
			color: $color-bunting;
			margin: 0 0 10px;
			transition: color $trans-default;
		}
		&-text {
			line-height: 171%;
			color: $color-waikawa-grey;
			transition: color $trans-default;
		}
		&:hover {
			background-color: $color-bunting;
		}
		&:hover & {
			&-num {
				color: rgba($color-default-white, 0.05);
			}
			&-icon {
				color: $color-default-white;
			}
			&-title {
				color: $color-default-white;
			}
			&-text {
				color: rgba($color-default-white, 0.6);
			}
		}
	}
}
