// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
//     https://www.htmlcsscolor.com/hex/334482

$color-neon-blue:                   #2c39f2;
$color-navy-blue:                   #116ACC;
$color-prussian-blue:               #004666;
$color-logan:	                     #9ca1bb;
$color-blue-zodiac:	               #424555;
$color-nobel:			               #999999;
$color-gainsboro:		               #DADADA;
$color-lavender:                    #EEEEFB;
$color-lavender-grey:               #B7BCD1;
$color-manatee:                     #8E909E;
$color-hawkes-blue:                 #D2D7E8;
$color-torch-red:                   #ff1553;
$color-outrageous-orange:           #FE603A;
$color-orange-red:		            #F33C12;
$color-harley-davidson-orange:      #D72C07;
$color-fern:				            #5BC75A;
$color-bunting:                     #2D3142;
$color-black-russian:               #1A1E2F;
$color-waikawa-grey:                #636B87;
$color-ghost-white:                 #fafafe;

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

//    html   
$fz-default:                        16px;

//     ios     
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;

$ff-montserrat:                    "Montserrat", sans-serif;
$ff-placeholder:                    "Placeholder", "Arial", sans-serif;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

//      $tf-default,     
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
